body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  max-width: 1000px; 
  border: 2px inset black;
  margin: 0 auto;
  background: grey;
  
}

/* App.js */
.small-container{
  width: 90%;
  margin: 0 auto;
  
}
.keep-as-row {
  display: flex;
  /* justify-content: space-between; */
}
.gameOptions {
  display: flex;
  justify-content: space-between;
}

/* AppTitle.js */
.title {
  text-align: center; 
  font-size: 14vh; 
  /* color: #001A9E; */
  color : #3894AC;
  background: #06445F;
  box-shadow: 2px 3px;
  margin-top : 0px;
  margin-bottom: 0px;
}

/* Home.js */
.column {
  border: 1px solid black;
  height: 4vh;
  width: 4vw;
  text-align: center; 
}
.show-grid {
  display: flex;
  width: 55%;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 35px;
}

/* Numbers.js */
.number-row {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.number {
  border : 1px solid black;
  height: 10vh;
  width: 4.5vw;
  text-align: center; 
  background-color: #00BAF6;
}
/* .number:hover {
  cursor: pointer;
  background: grey;
} */

/* Three By Three */
.s3x3 {
  width: 32.9993%;
  display: flex;
  flex-wrap: wrap;

  
}
.row3x3 {
  width: 100%;
  display: flex; 
  flex-direction: row;
}
/* Grid */
.grid-container {
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  
}
/* Cell */
.repeated {
  color: red; 
}
button{
  margin: 2px;
}
button:hover {
  cursor: pointer; 
}

.digits {
  display: flex;
  flex-direction: row;
  margin:20px;
}

.game-button {
  color: #00BAF6;
  background-color: black;
}

/* .timing {
  margin: 0 auto;
} */

/* Media query  */
@media (max-width: 500px){
  /* App.js */
  .keep-as-row {
    display: block;
    margin: 0 auto;
  }

  /* AppTitle.js */
  .title {
    font-size: 14vw;
  }


  /* Home.js */
  .column {
    width: 7vw;
  }
  .show-grid{
    width: 81%;
  }

}

